import { manPlayingVideos } from "assets";
import { RequestFailedException } from "exceptions";
import {
    ContactContentButton,
    ContactContentContainer,
    ContactContentForm,
    ContactContentFormContainer,
    ContactContentImage,
    ContactContentInput,
    ContactContentNameInputContainer,
    ContactContentSection,
    ContactContentTextArea
} from "pages/landing/sub-components/contact-content/ContactContent.styles";
import React, { ChangeEvent, Dispatch, FormEvent, SetStateAction, useState } from "react";
import { EmailService } from "services";

interface IContactContent {
    emailSuccessHandler?: () => void;
}

const ContactContent: React.FC<IContactContent> = ({ emailSuccessHandler }) => {
    const [email, setEmail] = useState('');
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [message, setMessage] = useState('');

    const resetField = () => {
        setEmail('');
        setFirstName('');
        setLastName('');
        setMessage('');
    };

    const sendEmail = async (event: FormEvent<HTMLFormElement>) => {
        event.preventDefault();

        try {
            const emailService = new EmailService();
            const values = { email, firstName, lastName, message };

            const result = await emailService.getInTouch(values);
            resetField();

            if (emailSuccessHandler)
                emailSuccessHandler();
        } catch (error) {
            if (error instanceof RequestFailedException) {
                alert(error.message);
            } else {
                alert('Something went wrong');
            }
        }
    };

    const updateState = (
        setState: Dispatch<SetStateAction<string>>
    ) => (event: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        setState(event.target.value);
    };

    return <ContactContentSection>
        <ContactContentContainer>
            <ContactContentImage src={manPlayingVideos} />

            <ContactContentFormContainer>
                <h1>Get In Touch</h1>

                <ContactContentForm onSubmit={sendEmail}>
                    <ContactContentNameInputContainer>
                        <ContactContentInput
                            placeholder="First Name"
                            value={firstName}
                            onChange={updateState(setFirstName)} />
                        <ContactContentInput
                            placeholder="Last Name"
                            value={lastName}
                            onChange={updateState(setLastName)} />
                    </ContactContentNameInputContainer>

                    <ContactContentInput
                        placeholder="Email Address"
                        value={email}
                        onChange={updateState(setEmail)} />

                    <ContactContentTextArea
                        placeholder="Message"
                        value={message}
                        onChange={updateState(setMessage)} />

                    <ContactContentButton>
                        <h5>Send</h5>
                    </ContactContentButton>
                </ContactContentForm>
            </ContactContentFormContainer>
        </ContactContentContainer>
    </ContactContentSection>;
};

export default ContactContent;
