import { useEffect, useRef } from "react";
import { motion, useAnimation, useInView } from "framer-motion";

import { CircularProgressBarProps } from "components/progress-bars/circular-progress-bar/CircularProgressBar.types";
import {
    CircularProgressBarContainer,
    CircularProgressBarPath,
    CircularProgressBarProgress,
    CircularProgressBarText
} from "components/progress-bars/circular-progress-bar/CircularProgressBar.styles";

const CircularProgressBar = (props: CircularProgressBarProps) => {
    const ref = useRef(null);
    const inView = useInView(ref);
    const circularProgressBarProgressAnimation = useAnimation();

    const size = props.size ?? 170;
    const strokeWidth = props.strokeWidth ?? 17;
    const radius = ((props.size ?? 170) - (props.strokeWidth ?? 17)) / 2;
    const value = props.value ?? 0;

    const updateCircularProgressBarProgressAnimation = (inView: boolean) => {
        const animation: Record<string, any> = {}

        if (inView) {
            animation.pathLength = value / 100
        } else {
            animation.pathLength = 0
        }

        circularProgressBarProgressAnimation.start(animation)
    }

    useEffect(() => {
        updateCircularProgressBarProgressAnimation(inView)
    }, [inView])

    return <CircularProgressBarContainer size={size} ref={ref}>
        <CircularProgressBarProgress
            value={value}
            radius={radius}
            height={size}
            width={size}>
            <defs>
                <linearGradient id="circularProgressBarGradientColor">
                    <stop offset="0%" stopColor="#9A2F74" />
                    <stop offset="100%" stopColor="#4A29AD" />
                </linearGradient>
            </defs>
            <motion.circle
                initial={{ pathLength: 0 }}
                animate={circularProgressBarProgressAnimation}
                transition={{ duration: props.transitionDuration ?? 2 }}
                cx={size / 2}
                cy={size / 2}
                r={radius}
                strokeWidth={strokeWidth}
                fill="transparent" />
        </CircularProgressBarProgress>

        <CircularProgressBarPath
            radius={radius}
            height={size}
            width={size}>
            <motion.circle
                initial={{ pathLength: 0 }}
                animate={{ pathLength: 1 }}
                transition={{ duration: 1 }}
                cx={size / 2}
                cy={size / 2}
                r={radius}
                stroke="transparent"
                strokeWidth={strokeWidth}
                fill="transparent" />
        </CircularProgressBarPath>

        <CircularProgressBarText>{props.text ?? `${value}%`}</CircularProgressBarText>
    </CircularProgressBarContainer>;
};

export default CircularProgressBar;
