import { ProjectCard } from "pages/landing/sub-components/projects-content/sub-components";
import { ProjectsContentContainer, ProjectsContentProjectContainer, ProjectsContentProjectsContainer, ProjectsContentSection } from "pages/landing/sub-components/projects-content/ProjectsContent.styles";
import { useEffect, useState } from "react";
import { ProjectService } from "services";
import { ProjectModel } from "models";

const ProjectsContent = () => {
    const [projects, setProjects] = useState<ProjectModel[]>([]);

    const fetchAllProjects = async () => {
        const projectService = new ProjectService();

        const allProjects = await projectService.getAllProjects();
        setProjects(allProjects);
    }

    useEffect(() => {
        fetchAllProjects();
    }, [])

    return <ProjectsContentSection>
        <ProjectsContentContainer>
            <h1>Projects</h1>

            <ProjectsContentProjectsContainer>
                {projects.map((obj, index) => <ProjectsContentProjectContainer key={index}>
                    <ProjectCard data={obj} />
                </ProjectsContentProjectContainer>)}
            </ProjectsContentProjectsContainer>
        </ProjectsContentContainer>
    </ProjectsContentSection>
};

export default ProjectsContent;
