import React, { useEffect, useState } from 'react';
import { backgroundLight, spaceman } from "assets";
import {
    LandingContainer,
    LandingBackgroundLightsImage,
    LandingContainerContent,
    LandingTagLine,
    LandingDisplayText,
    LandingDisplayTextCursor,
    LandingLetsConnect,
    LandingContainerSpaceman,
    LandingContentContainer,
} from "pages/landing/sub-components/landing-content/LandingContent.styles";

const LandingContent = () => {
    const [jobTitle, setJobTitle] = useState('');
    const [currentlyShowedJT, setCurrentlyShowedJT] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const [delta, setDelta] = useState(300 - Math.random() * 100);
    const jobList = ['Software Engineer', 'Web Developer'];

    const updateShowedJT = () => {
        const index = currentlyShowedJT % jobList.length;
        const fullText = jobList[index];
        const textToShow = isDeleting ? fullText.substring(0, jobTitle.length - 1) : fullText.substring(0, jobTitle.length + 1);

        setJobTitle(textToShow);

        if (isDeleting) {
            setDelta(prevDelta => prevDelta / 2);
        }

        if (!isDeleting && textToShow === fullText) {
            setIsDeleting(true);
            setDelta(2000);
        } else if (isDeleting && textToShow === '') {
            setIsDeleting(false);
            setCurrentlyShowedJT(currentlyShowedJT + 1);
            setDelta(500);
        }
    };

    useEffect(() => {
        const ticker = setInterval(() => {
            updateShowedJT();
        }, delta);

        return () => { clearInterval(ticker); };
    }, [jobTitle]);

    return <LandingContainer>
        <LandingContentContainer>
            <LandingBackgroundLightsImage
                src={`${backgroundLight}`}
                alt="" top={-400}
                left={-450}
                opacity={40} />

            <LandingBackgroundLightsImage
                src={`${backgroundLight}`}
                alt=""
                top={10}
                right={-450}
                opacity={20}
                width={1000} />

            <LandingContainerContent>
                <LandingTagLine>Welcome to my Portfolio</LandingTagLine>

                <LandingDisplayText>Hi! I'm Shan <LandingDisplayTextCursor>{jobTitle}</LandingDisplayTextCursor></LandingDisplayText>

                <p>All-rounder developer adept with both front-end and back-end, boasting
                    a significant involvement in projects as lead developer. Typescript,
                    JavaScript, PHP, Python, Java, Dart--name it, I can do it.</p>
                <p>Passionate and dedicated to developing outputs with efficient codes and
                    functional features, all to produce quality programs.</p>

                <a href="#"><LandingLetsConnect>Let's Connect <i className='bx bxs-chevron-right'></i></LandingLetsConnect></a>

            </LandingContainerContent>

            <LandingContainerSpaceman src={spaceman} />
        </LandingContentContainer>
    </LandingContainer>;
};

export default LandingContent;
