import { ProjectCardProps } from "pages/landing/sub-components/projects-content/sub-components/project-card/ProjectCard.types";
import {
    ProjectCardContainer,
    ProjectCardDescription,
    ProjectCardDetailsContainer,
    ProjectCardImage
} from "pages/landing/sub-components/projects-content/sub-components/project-card/ProjectCard.styles";
import { useState } from "react";

const ProjectCard = ({ data }: ProjectCardProps) => {
    const [isHovered, setIsHovered] = useState(false);

    const toggleHover = (value: boolean) => () => {
        setIsHovered(value);
    };

    return <ProjectCardContainer onMouseEnter={toggleHover(true)} onMouseLeave={toggleHover(false)}>
        <ProjectCardImage src={data.img ?? ''} />

        <ProjectCardDetailsContainer animate={isHovered ? 'hover' : 'rest'} >
            <h4>{data.name ?? ''}</h4>
            <ProjectCardDescription>{data.short_description ?? ''}</ProjectCardDescription>
        </ProjectCardDetailsContainer>
    </ProjectCardContainer>;
};

export default ProjectCard;
