class ProjectModel {
    public name: string;
    public tags: string;
    public short_description: string;
    public date_started: string;
    public img: string;

    constructor(data: Record<string, any>) {
        this.name = data.name;
        this.tags = data.tags;
        this.short_description = data.short_description;
        this.date_started = data.date_started;
        this.img = data.img;
    }

    static fromObjectList(objList: Record<string, any>[]): ProjectModel[] {
        const projectList: ProjectModel[] = [];

        for (const data of objList) {
            projectList.push(new ProjectModel(data))
        }

        return projectList;
    }
};

export default ProjectModel;
