import axios from "axios";
import { EnvironmentConstants } from "constants/index";
import { RequestFailedException } from "exceptions";

class EmailService {
    public async getInTouch(values: Record<string, any>) {
        const body = {
            "secretKey": EnvironmentConstants.SECRET_KEY,
            "secretValue": EnvironmentConstants.SECRET_VALUE,
            "action": "getInTouchPortfolio",
            "email": values.email,
            "firstName": values.firstName,
            "lastName": values.lastName,
            "message": values.message
        };

        const headers = {
            'Content-Type': 'text/plain'
        };

        const response = await axios.post(EnvironmentConstants.APP_SCRIPTS_URL, body, { headers });
        const { data } = response;

        if (data.statusCode !== 200) throw new RequestFailedException(data.data, data.statusCode);

        return data.data;
    }
}

export default EmailService;
