import React, { useEffect, useState } from 'react';

import { GitHubIcon, LinkedInIcon } from "components";
import {
    DefaultHeaderBurgerMenu,
    DefaultHeaderContainer,
    DefaultHeaderContentContainer,
    DefaultHeaderDownloadCVButton,
    DefaultHeaderMobileNavigation,
    DefaultHeaderNavigationContainer,
    DefaultHeaderCloseNavButton
} from "components/layouts/headers/default-header/DefaultHeader.styles";
import { Link } from "react-router-dom";
import LinkService from 'services/links/LinkService';

const DefaultHeader = () => {
    const [resumeDownload, setResumeDownload] = useState<string>();
    const [showMobileNav, setShowMobileNav] = useState(false);

    const toggleMobileNav = () => {
        setShowMobileNav(prevVal => !prevVal);
    };

    const fetchResumeDownloadLink = async () => {
        try {
            setResumeDownload(await LinkService.getResumeLink());
        } catch (exception) {
            setResumeDownload("/portfolio/storage/downloadables/Resume.pdf");
        }
    };

    useEffect(() => {
        fetchResumeDownloadLink();
    }, []);

    return <DefaultHeaderContainer>
        <DefaultHeaderContentContainer>
            <Link to="/portfolio"><h3>SHAN</h3></Link>

            <DefaultHeaderNavigationContainer>
                <Link to="/portfolio"><h4>Home</h4></Link>

                <a target="_blank" href="https://www.linkedin.com/in/shanpadayhag/"><LinkedInIcon /></a>
                <a target="_blank" href="https://github.com/shanpadayhag"><GitHubIcon /></a>

                <DefaultHeaderDownloadCVButton target="_blank" href={resumeDownload} className={resumeDownload ? '' : 'hidden'}>
                    <h4>Resume</h4>
                </DefaultHeaderDownloadCVButton>
            </DefaultHeaderNavigationContainer>

            <DefaultHeaderBurgerMenu href="#" onClick={toggleMobileNav}><i className='bx bx-menu-alt-right'></i></DefaultHeaderBurgerMenu>
            <DefaultHeaderMobileNavigation animate={showMobileNav ? 'open' : 'close'}>
                <DefaultHeaderCloseNavButton href="#" onClick={toggleMobileNav}><i className='bx bx-x'></i></DefaultHeaderCloseNavButton>
                <ul>
                    <li><Link to="/portfolio" onClick={toggleMobileNav}><h4>Home</h4></Link></li>
                    <li><a target="_blank" href="https://www.linkedin.com/in/shanpadayhag/" onClick={toggleMobileNav}><h4>LinkedIn</h4></a></li>
                    <li><a target="_blank" href="https://github.com/shanpadayhag" onClick={toggleMobileNav}><h4>GitHub</h4></a></li>
                    <li className={resumeDownload ? '' : 'hidden'}><a target="_blank" href={resumeDownload} onClick={toggleMobileNav}><h4>Resume</h4></a></li>
                </ul>
            </DefaultHeaderMobileNavigation>
        </DefaultHeaderContentContainer>
    </DefaultHeaderContainer>;
};

export default DefaultHeader;
